
/*---　youtube　---*/
.youtube {
	margin-bottom: $textBottom;
	iframe {
		@extend %m0a;
	}
}

@include query($sp) {
	/*---　youtube　---*/
	.youtube {
		position: relative;
		width: 100%;
		padding-top: 56.25%;
		iframe {
			position: absolute;
			top: 0;
			right: 0;
			width: 100% !important;
			height: 100% !important;
		}
	}
}

