/*===　utill ===*/
@for $i from 0 through 14 {
	.mt#{$i * 5} { margin-top: 5px * $i !important; }
	.mr#{$i * 5} { margin-right: 5px * $i !important; }
	.mb#{$i * 5} { margin-bottom: 5px * $i !important; }
	.ml#{$i * 5} { margin-left: 5px * $i !important; }
	.pt#{$i * 5} { padding-top: 5px * $i !important; }
	.pr#{$i * 5} { padding-right: 5px * $i !important; }
	.pb#{$i * 5} { padding-bottom: 5px * $i !important; }
	.pl#{$i * 5} { padding-left: 5px * $i !important; }
}

@include query($sp) {
	@for $i from 0 through 14 {
		.sp_mt#{$i * 5} { margin-top: 5px * $i !important; }
		.sp_mr#{$i * 5} { margin-right: 5px * $i !important; }
		.sp_mb#{$i * 5} { margin-bottom: 5px * $i !important; }
		.sp_ml#{$i * 5} { margin-left: 5px * $i !important; }
		.sp_pt#{$i * 5} { padding-top: 5px * $i !important; }
		.sp_pr#{$i * 5} { padding-right: 5px * $i !important; }
		.sp_pb#{$i * 5} { padding-bottom: 5px * $i !important; }
		.sp_pl#{$i * 5} { padding-left: 5px * $i !important; }
	}
}
