@charset "UTF-8";
a, p, h1, h2, h3, h4, h5, th, td, dt, dl, li {
  line-height: 1.8;
}

.youtube iframe {
  margin: 0 auto;
}

.btn {
  transition: 0.3s ease;
}

.icon_link:after, .icon_pdf:after {
  content: '';
  display: inline-block;
  position: relative;
  top: 2px;
  margin: 0 3px;
