/*=== l-footer ===*/
/*---　gotop　---*/
.gotop {
  display: block;
  width: 45px;
  height: 45px;
  text-align: center;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 2;
}

.gotop span {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 14px 8px;
  border-color: transparent transparent #fff transparent;
}

.gotop a {
  display: block;
  width: 45px;
  height: 45px;
  line-height: 1;
  background-color: #00afd6;
}

