===================================*/
/*===　btn 　===*/
.btn {
  display: inline-block;
  padding: 10px 25px;
  text-align: center;
  font-size: 16px;
}

.btn_standard {
  color: #00afd6;
  border: 1px solid;
}

.btn_standard:hover {
  background-color: #00afd6;
  color: #FFF;
}

.btn_error {
  color: #e03535;
  border: 1px solid;
}

.btn_error:hover {
  background-color: #e03535;
  color: #FFF;
}

.btn_warning {
  color: #f9a712;
  border: 1px solid;
}

.btn_warning:hover {
  background-color: #f9a712;
  color: #FFF;
}

.btn_small {
  font-size: 14px;
  padding: 5px 10px;
}

.btn_big {
  width: 100%;
}

.btn_radius {
  border-radius: 5px;
}

@media screen and (max-width: 640px) {
  .btn {
    padding: 10px 20px;
    font-size: 14px;
  }
  .btn_small {
    font-size: 14px;
    padding: 0px 10px;
  }
}

