/*---　キャプション　---*/
.caption {
  display: table;
}

.caption figcaption {
  display: table-caption;
  caption-side: bottom;
  margin-top: 10px;
  margin-bottom: 30px;
  font-size: 12px;
  line-height: 1.3;
  padding: 0;
}

@media screen and (max-width: 640px) {
  /*---　要素の配置　---*/
  .alignleft, .alignright {
    display: flex;
    justify-content: center;
    float: none;
    margin: 0 0 15px;
  }
  /*---　キャプション　---*/
}

.list_disc {
  margin-left: 10px;
}

.list_disc > li {
  margin-bottom: 5px;
  position: relative;
  padding-left: 14px;
}

.list_disc > li:before {
  content: "";
  display: block;
  width: 3px;
  height: 3px;
  background: #323232;
  border-radius: 50%;
  position: absolute;
  top: 13px;
  left: 0;
}

.list_disc > li:last-child {
  margin-bottom: 0;
}

.list_circle {
  margin-left: 10px;
}

.list_circle > li {
  margin-bottom: 5px;
  position: relative;
  padding-left: 14px;
}

.list_circle > li:before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
  border: #333 2px solid;
  position: absolute;
  top: 10px;
  left: 0;
}

.list_circle > li:last-child {
  margin-bottom: 0;
}

.list_number > li {
  margin-bottom: 5px;
  position: relative;
  margin-left: 20px;
  list-style: decimal;
}

.list_number > li:last-child {
  margin-bottom: 0;
}

@media screen and (max-width: 640px) {
  .list_disc > li:before {
    top: 10px;
  }
  .list_circle > li:before {
    top: 8px;
  }
}

/*===　media　===*/
.media {
  display: flex;
}

.media_left {
  padding-right: 10px;
}

.media_right {
  padding-left: 10px;
}

/*---　youtube　---*/
.youtube {
  margin-bottom: 30px;
}

@media screen and (max-width: 640px) {
  /*---　youtube　---*/
  .youtube {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
  }
  .youtube iframe {
    position: absolute;
    top: 0;
    right: 0;
    width: 100% !important;
    height: 100% !important;
  }
}

/*===　sprite/SVG　===*/
/*---　sprite　---*/
.sprite01 {
  display: inline-block;
}

.sprite01 {
  background-image: url(../../assets/images/sprite.png);
  background-position: 0px -52px;
