}

@media screen and (max-width: 640px) {
  /*---　sprite　---*/
  .sprite01 {
    background-image: url(/wp-content/themes/wp-template/assets/images/sprite_sp.png);
    background-position: 0px 0px;
    width: 79.5px;
    height: 83px;
    background-size: 79.5px 83px;
  }
  /*---　SVG　---*/
  .svg01 {
