/*===　table　===*/
.table th {
  font-weight: bold;
  text-align: center;
}

.table th,
.table td {
  padding: 8px 10px;
}

.table_layout01 th {
  background-color: #dbf4f9;
  color: #00afd6;
}

.table_border th,
.table_border td {
  border: solid 1px #ccc;
}

.table_center {
  margin: 0 auto 30px auto;
}

.table_center th {
  text-align: center;
}

.table_center th,
.table_center td {
  padding: 8px 10px;
}

.table_stripe tbody tr:nth-of-type(even) {
  background-color: #f9f9f9;
}

.table_responsive {
  overflow-x: auto;
}

.table_responsive table {
  width: 100%;
}

.table_responsive table * {
  word-break: normal;
}

@media screen and (max-width: 640px) {
  .table th,
  .table td {
    padding: 8px 10px;
  }
  .table_center {
    margin: 0 auto 15px auto;
  }
}

